<template>
  <div id="announce">
    <new-announce-slider></new-announce-slider>
    <div class="announce_list">






      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <van-list
          v-model="loading"
          :finished="finished"
          @load="onLoad"
        >

          <ul>
            <li v-for="(item, index) in announceList" :key="index">
              <div class="left">

              <img v-lazy="getImgURL(item.thumbnailUrl.thumbnailPelativePath)"
                     @click="$router.push({
                                            path: detailURL,
                                            query:{id:item.id}
                                         })">
              </div>
              <div class="right">
                <h3 class="tit text-overflow"
                    @click="$router.push({
                                          path: detailURL,
                                          query:{id:item.id}
                                        })">
                  {{ item.title }}
                </h3>
                <div class="con con-overflow" v-html="item.content">{{ item.content }}</div>
                <p class="date">{{ timeStamp(item.publishTime) }}</p>
              </div>
            </li>
          </ul>
        </van-list>
        <div class="no_more" v-show="finished">没有更多了......</div>
      </van-pull-refresh>
    </div>

  </div>
</template>

<script>
  export default {
    name:"announce",
    data(){
      return{
        detailURL: '/announceDetail',
        announceList: [],

        loading: false,
        finished: false,
        isLoading: false,
        pageNum: 1, // 页数
        offset: 0,  // 下次加载起始页
        totalPage: 0 // 总页数
      }
    },

    methods:{
      async init(){
        let that = this;

        let params = {
          offset: (that.pageNum - 1) * 8
        };

        let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/messageList', params);
       that.announceList = res.result.messages.data;
        that.pageNum = res.result.messages.page; // 当前显示页
        that.totalPage = Math.ceil(res.result.messages.total / res.result.messages.limit); // 总页数
        if(res.result.messages.total<8) {
          that.finished = true; // 加载结束
          that.isLoading = false;
          that.loading = false;
          return false;
        }
       },

      async concatData(){
        let that = this;
        // that.pageNum += 1;
        that.pageNum += 1;

        let params = {
          offset: (that.pageNum - 1) * 8
        };

        let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/messageList', params);
        if(res.result.messages.total<8) {
          that.finished = true; // 加载结束
          that.isLoading = false;
          that.loading = false;
          return false;
        }
        that.announceList = that.announceList.concat(res.result.messages.data);
        that.loading = false;

        if(that.pageNum >= that.totalPage){
          that.finished = true; // 加载结束
        }

      },

      onRefresh() {
        let that = this;
         setTimeout(() => {
          this.$toast({
            message: '刷新成功',
            position: 'bottom'
          });
          that.isLoading = false;
          that.pageNum = 1;
          that.loading = false;
          that.finished = false;
          that.isLoading = false;
          that.init();
        }, 500);

      },

      onLoad() {
        // 异步更新数据
        setTimeout(() => {
          this.concatData();
        }, 500);
      }
    },

    created(){
      this.init();
    }
  }
</script>

<style scoped lang="scss">
  .no_more{
    font-size: 30px;
    color: #8d8d8d;
    text-align: center;
    margin: 40px auto 0;
  }

  .announce_list{

    position: relative;
    top: 460px;
    margin-bottom: 50px;

    ul{
      width: 93%;
      margin: auto;
      background: #FFF;
      padding-top: 30px;

      li{
        height: 150px;
        display: flex;
        padding-bottom: 25px;
        padding-left: 20px;

        &:not(:last-child){
          border-bottom: 5px solid #F5F5F5;
          margin-bottom: 25px;
        }

        .left{
          flex: .35;
          padding-right: 25px;
        }

        .right{
          flex: 1;
          min-width: 0; //解决文字溢出white-space和flex不兼容问题
          position: relative;

          h3{
            color: #000;
            font-size: 30px;
            padding-bottom: 5px;
          }

          .con{
            color: #8D8D8D;
            font-size: 28px;
          }

          .date{
            font-size: 28px;
            position: absolute;
            left: 0;
            bottom: 0;
          }
        }
      }
    }
  }
</style>



